.games_wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 20px 40px;
    margin: 0 auto;
    max-width: 700px;   
}

.game_wrap_title{
    width: 320px;
    background: -o-radial-gradient(50% 50%,50% 50%,#374529 0,#0f140a 100%);
    background: radial-gradient(50% 50% at 50% 50%,#374529 0,#0f140a 100%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding:5px;
    margin-bottom: 20px;
    max-width:350px ;
    cursor: pointer;
    border-radius: 10px;
}

.game_title{
    font-weight: 600;
    font-size: Min(20px, 4vw);
    margin-left: 20px;
}

.catalog_instruction {
    min-width: 280px;
    max-width: 500px;
    padding: 5px;
    background-color: #fff;
    border-radius: 5px;
    background: -o-radial-gradient(50% 50%,50% 50%,#374529 0,#0f140a 100%);
    background: radial-gradient(50% 50% at 50% 50%,#374529 0,#0f140a 100%);
    line-height: 120%;
}

.catalog_instruction p:first-child {
    margin-bottom: 10px;
}

.catalog_instruction a {
    border-bottom: 1px solid #374529;
}

.catalog_instruction a:hover {
    border: 1px solid #374529;
}
